<template>
  <div>
    <div
      style="
        display: flex;
        align-items: center;
        margin-top: 20px;
        margin-bottom: 20px;
      "
    >
      <h1 style="flex: 1; text-align: center; margin: 0">bookManage</h1>

      <el-input
        v-model="input"
        placeholder="Please enter the book name."
        style="width: 20%"
        clearable
      ></el-input>

      <el-button @click="searchBook" style="margin-right: 50px"
        >searchBook</el-button
      >
      <el-button type="primary" @click="startAdd" style="margin-right: 50px"
        >addBook</el-button
      >
      <el-button type="success" @click="startReturn" style="margin-right: 100px"
        >returnBook</el-button
      >
    </div>

    <el-table
      :data="bookList"
      class="el-table"
      stripe
      highlight-current-row
      empty-text="There is currently no book list available!"
      :header-cell-style="{ textAlign: 'center', background: '#fafafa' }"
      :cell-style="{ textAlign: 'center' }"
      style=""
    >
      <el-table-column type="index" label="" />

      <el-table-column prop="bookname" label="Book Name"> </el-table-column>

      <el-table-column prop="catagory" label="catagory"> </el-table-column>

      <el-table-column prop="location" label="location"> </el-table-column>

      <el-table-column prop="remain" label="remain"> </el-table-column>

      <el-table-column label="Details">
        <template slot-scope="scope">
          <el-button type="info" @click="showDetails(scope.$index)">
            Details
          </el-button>
        </template>
      </el-table-column>

      <el-table-column label="Operations">
        <template #default="scope">
          <div style="align-items: center; padding: 2px">
            <el-button type="warning" @click="startEdit(scope.$index)"
              >Update</el-button
            >

            <el-button type="danger" @click="deleteBook(scope.$index)"
              >Delete</el-button
            >
          </div>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="book details" :visible.sync="detailsDialog">
      <el-descriptions>
        <el-descriptions-item label="book name" :span="1">{{
          detailFrom.bookname
        }}</el-descriptions-item>

        <el-descriptions-item label="press" :span="1">{{
          detailFrom.press
        }}</el-descriptions-item>
        <el-descriptions-item label="author" :span="1">{{
          detailFrom.author
        }}</el-descriptions-item>
        <el-descriptions-item label="publishtime" :span="1">{{
          detailFrom.publishtime
        }}</el-descriptions-item>
        <el-descriptions-item label="catagory" :span="1">{{
          detailFrom.catagory
        }}</el-descriptions-item>
        <el-descriptions-item label="remain" :span="1"
          >{{ detailFrom.remain }}
        </el-descriptions-item>
        <el-descriptions-item label="location" :span="1">
          {{ detailFrom.location }}
        </el-descriptions-item>
        <el-descriptions-item label="isbn" :span="1">
          {{ detailFrom.isbn }}
        </el-descriptions-item>
      </el-descriptions>
      <el-descriptions>
        <el-descriptions-item label="introduction" :span="4">
          {{ detailFrom.introduction }}
        </el-descriptions-item>
      </el-descriptions>
      <!-- <button @click="endDetails">end</button> -->
    </el-dialog>

    <el-dialog
      :title="this.title[dialogFlag - 1]"
      :visible.sync="visible"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <el-form :model="dialogForm" label-width="120px">
        <el-form-item v-if="dialogFlag == 2" label="bookname:">
          <el-input
            v-model="dialogForm.bookname"
            placeholder="Please enter bookname"
            clearable
          />
        </el-form-item>

        <el-form-item v-if="dialogFlag == 2" label="press:">
          <el-input
            v-model="dialogForm.press"
            placeholder="Please enter press"
            clearable
          />
        </el-form-item>

        <el-form-item v-if="dialogFlag == 2" label="author:">
          <el-input
            v-model="dialogForm.author"
            placeholder="Please enter author"
            clearable
          />
        </el-form-item>

        <el-form-item v-if="dialogFlag == 2" label="publishtime:">
          <!--date-->
          <el-date-picker
            type="date"
            placeholder="select data"
            v-model="dialogForm.publishtime"
            style="width: 100%"
            @change="handleDateChange"
          ></el-date-picker>
        </el-form-item>

        <el-form-item v-if="dialogFlag == 2" label="catagory:">
          <el-input
            v-model="dialogForm.catagory"
            placeholder="Please enter catagory"
            clearable
          />
        </el-form-item>

        <el-form-item v-if="dialogFlag == 2" label="introduction:"
          ><!--text-->
          <el-input
            v-model="dialogForm.introduction"
            placeholder="Please enter introduction"
            clearable
          />
        </el-form-item>

        <el-form-item v-if="dialogFlag == 1" label="isbn:">
          <el-row>
            <el-col :span="16">
              <el-input
                v-model="dialogForm.isbn"
                placeholder="Please enter isbn."
                clearable
              ></el-input>
            </el-col>
            
          </el-row>
        </el-form-item>
        <!-- 
        <el-descriptions v-if="dialogFlag == 1" style="margin-bottom: 20px">
          <el-descriptions-item label="book name" :span="1">{{
            dialogForm.bookname
          }}</el-descriptions-item>

          <el-descriptions-item label="press" :span="1">{{
            dialogForm.press
          }}</el-descriptions-item>
          <el-descriptions-item label="author" :span="1">{{
            dialogForm.author
          }}</el-descriptions-item>
          <el-descriptions-item label="publishtime" :span="1">{{
            dialogForm.publishtime
          }}</el-descriptions-item>
          <el-descriptions-item label="catagory" :span="1">{{
            dialogForm.catagory
          }}</el-descriptions-item>
        </el-descriptions> -->

        <!-- <el-descriptions v-if="dialogFlag == 2
        " style="margin-bottom: 20px">
          <el-descriptions-item label="remain" :span="1">{{
            dialogForm.remain
          }}</el-descriptions-item>
        </el-descriptions> -->

        <el-form-item label="remain:" v-if="dialogFlag == 1"
          ><!--int-->
          <el-input
            v-model.number="dialogForm.remain"
            placeholder="Please enter remain"
            clearable
          />
        </el-form-item>

        <el-form-item label="location:">
          <el-input
            v-model="dialogForm.location"
            placeholder="Please enter location"
            clearable
          />
        </el-form-item>

        <el-table
        v-if="dialogFlag == 1"
          :data="bar_codes"
          class="el-table"
          stripe
          highlight-current-row
          empty-text="There is currently no barcode list available!"
          :header-cell-style="{ textAlign: 'center', background: '#fafafa' }"
          :cell-style="{ textAlign: 'center' }"
          style=""
        >
        <el-table-column type="index" label="" />

        <el-table-column prop="barcode" label="Barcode">
        <template slot-scope="scope">
          <img :src="scope.row.barcode" alt="Barcode" />
        </template>
      </el-table-column>

  
        </el-table>
      </el-form>

      <div v-if="dialogFlag == 1" class="button-container">
        <div class="button-group">
          <el-button size="small" @click="cancelAdd">cancel</el-button>
          <el-button size="small" type="primary" @click="add"
            >confirm</el-button
          >
        </div>
      </div>

      <div v-else-if="dialogFlag == 2" class="button-container">
        <div class="button-group">
          <el-button size="small" @click="cancel">cancel </el-button>
          <el-button size="small" type="primary" @click="editBook"
            >confirm</el-button
          >
        </div>
      </div>
    </el-dialog>

    <el-dialog
      title="return book"
      :visible.sync="returnDialog"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <el-form label-width="120px">
        <el-form-item label="barcode:">
          <el-input
            v-model="borrowid"
            placeholder="Please enter barcode"
            clearable
          />
        </el-form-item>
      </el-form>
      <div class="button-container">
        <div class="button-group">
          <el-button size="small" @click="cancel_return">cancel </el-button>
          <el-button size="small" type="primary" @click="returnBook"
            >confirm</el-button
          >
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/api/api";
import axios from "axios";

export default {
  data() {
    return {
      bookList: [],
      visible: false,
      dataListLoading: false,
      title: ["add book", "edit book"],
      url: "http://localhost:8080/staff",
      detailsDialog: false,
      detailFrom: {
        press: null,
        author: null,
        publishtime: null,
        remain: null,
        catagory: null,
        introduction: null,
        location: null,
      },

      dialogFlag: 0,
      dialogForm: {
        bookname: null,
        press: null,
        author: null,
        publishtime: null,
        remain: null,
        catagory: null,
        introduction: null,
        location: null,
        isbn: null,
      },

      borrowid: null,
      returntime: null,
      returnDialog: false,
      input: "",
      bar_codes: [],
    };
  },
  mounted() {
    this.getBookList();
    console.log("mounted");
  },
  methods: {
    getBookList() {
      console.log("getBookList");
      axios
        //.get("http://localhost:8080/staff/book/show/all")
        .get(`${this.url}/book/show/all`)
        .then((res) => {
          // 处理响应数据
          console.log("sucess");
          console.log(res);
          this.bookList = res.data;

          this.dataListLoading = true;
        })
        .catch((error) => {
          // 处理请求错误
          console.log("getBookList失败了");
        });
    },
    showDetails(index) {
      this.detailsDialog = true;
      console.log("showDetails的detailsDialog:", this.detailsDialog);
      console.log("index", index);

      this.detailFrom = this.bookList[index];
      console.log(this.showFrom);
    },
    /*
    endDetails() {
      this.detailsDialog = false;
      console.log("endDetails的detailsDialog:", this.detailsDialog);
    },*/
    startAdd() {
      this.visible = true;
      this.dialogFlag = 1;
    },

    addBook() {
      //  /book/add/{bookname}/{press}/{author}/{publishtime}/{catagory}/{remain}/{introduction}/{location}
      console.log("this.dialogForm.bookname:", this.dialogForm.bookname);
      console.log("this.dialogForm.press:", this.dialogForm.press);
      console.log("this.dialogForm.author:", this.dialogForm.author);
      console.log("this.dialogForm.publishtime:", this.dialogForm.publishtime);
      console.log("this.dialogForm.catagory:", this.dialogForm.catagory);
      console.log("this.dialogForm.remain:", this.dialogForm.remain);
      console.log(
        "this.dialogForm.introduction:",
        this.dialogForm.introduction
      );
      console.log("this.dialogForm.location:", this.dialogForm.location);

      if (
        this.dialogForm.bookname == null ||
        this.dialogForm.bookname == undefined ||
        this.dialogForm.bookname == ""
      ) {
        this.$message({
          message: "bookname cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.press == null ||
        this.dialogForm.press == undefined ||
        this.dialogForm.press == ""
      ) {
        this.$message({
          message: "press cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.author == null ||
        this.dialogForm.author == undefined ||
        this.dialogForm.author == ""
      ) {
        this.$message({
          message: "author cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.publishtime == null ||
        this.dialogForm.publishtime == undefined ||
        this.dialogForm.publishtime == ""
      ) {
        this.$message({
          message: "publishtime cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.catagory == null ||
        this.dialogForm.catagory == undefined ||
        this.dialogForm.catagory == ""
      ) {
        this.$message({
          message: "catagory cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.remain == null ||
        this.dialogForm.remain == undefined ||
        this.dialogForm.remain == ""
      ) {
        this.$message({
          message: "remain cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.introduction == null ||
        this.dialogForm.introduction == undefined ||
        this.dialogForm.introduction == ""
      ) {
        this.$message({
          message: "introduction cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.location == null ||
        this.dialogForm.location == undefined ||
        this.dialogForm.location == ""
      ) {
        this.$message({
          message: "location cannot be empty！",
          type: "warning",
        });
      } else {
        const jsDate = this.dialogForm.publishtime;
        // 获取年、月、日
        const year = jsDate.getFullYear();
        const month = String(jsDate.getMonth() + 1).padStart(2, "0"); // 月份从 0 开始，需要加 1
        const day = String(jsDate.getDate()).padStart(2, "0");
        // 组合为数据库日期字符串
        const dbDate = `${year}-${month}-${day}`;
        console.log("date:", dbDate);
        axios
          .post(
            `${this.url}/book/add/${this.dialogForm.bookname}/${this.dialogForm.press}/${this.dialogForm.author}/${dbDate}/${this.dialogForm.catagory}/${this.dialogForm.remain}/${this.dialogForm.introduction}/${this.dialogForm.location}`
          )
          .then((res) => {
            // 处理响应数据
            console.log("addBook成功了", res);
            this.getBookList();
            this.dialogForm.bookname = null;
            this.dialogForm.press = null;
            this.dialogForm.author = null;
            this.dialogForm.publishtime = null;
            this.dialogForm.catagory = null;
            this.dialogForm.remain = null;
            this.dialogForm.introduction = null;
            this.dialogForm.location = null;
            this.dialogForm.isbn = null;

            this.$message({
              message: "Successfully added!",
              type: "success",
            });
          })
          .catch((error) => {
            // 处理请求错误
            console.log("addBook失败了");
            this.$message({
              message:
                "Adding failed! Please try again or contact the developer.",
              type: "warning",
            });
          });

        this.visible = false;
      }
    },
    startEdit(index) {
      this.visible = true;
      this.dialogFlag = 2;
      //  @PutMapping("/book/update/{bookid}/{bookname}/{press}/{author}/{publishtime}/{catagory}/{remain}/{introduction}/{location}")
      console.log("要编辑的那一条", this.bookList[index]);
      this.dialogForm.bookid = this.bookList[index].bookid;
      this.dialogForm.bookname = this.bookList[index].bookname;
      this.dialogForm.press = this.bookList[index].press;
      this.dialogForm.author = this.bookList[index].author;
      //this.dialogForm.publishtime = this.bookList[index].publishtime;
      this.dialogForm.publishtime = new Date(this.bookList[index].publishtime);
      console.log(
        "staredit的this.dialogForm.publishtime：",
        this.dialogForm.publishtime
      );
      this.dialogForm.catagory = this.bookList[index].catagory;
      this.dialogForm.remain = this.bookList[index].remain;
      this.dialogForm.introduction = this.bookList[index].introduction;
      this.dialogForm.location = this.bookList[index].location;
    },

    editBook(index) {
      if (
        this.dialogForm.bookname == null ||
        this.dialogForm.bookname == undefined ||
        this.dialogForm.bookname == ""
      ) {
        this.$message({
          message: "bookname cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.press == null ||
        this.dialogForm.press == undefined ||
        this.dialogForm.press == ""
      ) {
        this.$message({
          message: "press cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.author == null ||
        this.dialogForm.author == undefined ||
        this.dialogForm.author == ""
      ) {
        this.$message({
          message: "author cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.publishtime == null ||
        this.dialogForm.publishtime == undefined ||
        this.dialogForm.publishtime == ""
      ) {
        this.$message({
          message: "publishtime cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.catagory == null ||
        this.dialogForm.catagory == undefined ||
        this.dialogForm.catagory == ""
      ) {
        this.$message({
          message: "catagory cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.remain == null ||
        this.dialogForm.remain == undefined ||
        this.dialogForm.remain == ""
      ) {
        this.$message({
          message: "remain cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.introduction == null ||
        this.dialogForm.introduction == undefined ||
        this.dialogForm.introduction == ""
      ) {
        this.$message({
          message: "introduction cannot be empty！",
          type: "warning",
        });
      } else if (
        this.dialogForm.location == null ||
        this.dialogForm.location == undefined ||
        this.dialogForm.location == ""
      ) {
        this.$message({
          message: "location cannot be empty！",
          type: "warning",
        });
      } else {
        console.log("edit的publishtime", this.dialogForm.publishtime);
        const jsDate = this.dialogForm.publishtime;
        // 获取年、月、日
        const year = jsDate.getFullYear();
        const month = String(jsDate.getMonth() + 1).padStart(2, "0"); // 月份从 0 开始，需要加 1
        const day = String(jsDate.getDate()).padStart(2, "0");
        // 组合为数据库日期字符串
        const dbDate = `${year}-${month}-${day}`;
        console.log("date:", dbDate);
        axios
          .put(
            `${this.url}/book/update/${this.dialogForm.bookid}/${this.dialogForm.bookname}/${this.dialogForm.press}/${this.dialogForm.author}/${dbDate}/${this.dialogForm.catagory}/${this.dialogForm.remain}/${this.dialogForm.introduction}/${this.dialogForm.location}`
          )
          .then((res) => {
            // 处理响应数据
            console.log("edit Book成功了", res);
            this.getBookList();
            this.dialogForm.bookname = null;
            this.dialogForm.press = null;
            this.dialogForm.author = null;
            this.dialogForm.publishtime = null;
            this.dialogForm.catagory = null;
            this.dialogForm.remain = null;
            this.dialogForm.introduction = null;
            this.dialogForm.location = null;
            this.dialogForm.isbn = null;
            this.$message({
              message: "Successfully edited!",
              type: "success",
            });
          })
          .catch((error) => {
            // 处理请求错误
            console.log("editbook失败了");
            this.$message({
              message:
                "Editing failed! Please try again or contact the developer.",
              type: "warning",
            });
          });
        this.visible = false;
      }
    },
    handleDateChange() {
      console.log("date change:", this.dialogForm.publishtime); // 打印所选日期，可根据实际需求进行处理
    },

    cancel() {
      this.dialogForm.bookname = null;
      this.dialogForm.press = null;
      this.dialogForm.author = null;
      this.dialogForm.publishtime = null;
      this.dialogForm.catagory = null;
      this.dialogForm.remain = null;
      this.dialogForm.introduction = null;
      this.dialogForm.location = null;
      this.dialogForm.isbn = null;
      this.visible = false;
    },
    deleteBook(index) {
      this.$confirm(`Are you sure to delete this book`, "提示", {
        confirmButtonText: "confirm",
        cancelButtonText: "cancel",
        type: "warning",
      }).then(() => {
        let id = this.bookList[index].bookid;
        console.log("要被删除的 bookid:", id);
        axios
          //"/book/delete/{bookid}")
          .delete(`${this.url}/book/delete/${id}`)
          .then((res) => {
            this.getBookList();
            console.log("调用删除成功,res.data", res.data);
            if (res.data.length > 1 || res.data[0] > 0) {
              let tmp = "";
              for (let i = 0; i < res.data.length; i++) {
                tmp += res.data[i];
                if (i < res.data.length - 1) tmp += ",";
              }
              let m = "Users with userid ";
              m += tmp;
              m += " are currently borrowing this book!";
              //Users with userid 1,2 are currently borrowing this book
              this.$message({
                message: m,
                type: "warning",
              });
            } else if (res.data == -1) {
              this.$message({
                message: "Successfully deleted!",
                type: "success",
              });
            } else if (res.data == -2) {
              this.$message({
                message:
                  "Delete failed! Please try again or contact the developer.",
                type: "warning",
              });
            }
          })
          .catch((error) => {
            console.log("删除失败了");
            this.$message({
              message:
                "Delete failed! Please try again or contact the developer.",
              type: "warning",
            });
          });
      });
    },
    startReturn() {
      this.returnDialog = true;
    },
    cancel_return() {
      this.returnDialog = false;
    },
    returnBook(index) {
      this.$confirm(`Are you sure you want to return this book?`, "提示", {
        confirmButtonText: "confirm",
        cancelButtonText: "cancel",
        type: "warning",
      }).then(() => {
        if (
          this.borrowid == null ||
          this.borrowid == undefined ||
          this.borrowid == ""
        ) {
          this.$message({
            message: "Barcode cannot be empty！",
            type: "warning",
          });
        } else {
          console.log("要被删除的 Barcode:", this.borrowid);

          // 获取当前日期

          const today = new Date();
          // 获取年、月、日
          const year = today.getFullYear();
          const month = today.getMonth() + 1; // 注意月份从0开始，所以要加1
          const day = today.getDate();

          // 组合为数据库日期字符串
          const dbDate = `${year}-${month}-${day}`;
          console.log("今天的date:", dbDate);
          axios
            //"/book/delete/{bookid}")
            .get(`${this.url}/book/return/${this.borrowid}/${dbDate}`)
            .then((res) => {
              console.log("还书方法调用成功", res.data);
              if (res.data == 1) {
                this.$message({
                  message: "Successfully returned!",
                  type: "success",
                });
                this.borrowid = null;
                this.returntime = null;
                this.returnDialog = false;
              } else if (res.data == -1) {
                this.$message({
                  message: "The barcode entered does not exist.",
                  type: "warning",
                });
              } else if (res.data == -2) {
                this.$message({
                  message: "The book has already been returned.",
                  type: "warning",
                });
              } else if (res.data == -3) {
                this.$message({
                  message:
                    "The return date is earlier than the borrowing date.",
                  type: "warning",
                });
              } else if (res.data == 0) {
                this.$message({
                  message: "Wrong.",
                  type: "warning",
                });
              }
            })
            .catch((error) => {
              console.log("还书失败了", error);
              this.$message({
                message:
                  "Book return failed!The barcode entered may not exist.Alternatively,please try again or contact the developer.",
                type: "warning",
              });
            });
        }
      });
    },
    searchBook() {
      console.log("input:", this.input);
      if (this.input == "" || this.input == undefined || this.input == null) {
        this.getBookList();
        return;
      } else {

        axios
          ///book/get/{bookname}"
          .get(`${this.url}/book/get/${this.input}`)
          .then((res) => {
            console.log("searchBook sucess");
            console.log(res);
            this.bookList = [];
            this.bookList[0] = res.data;
       
            this.$message({
              message: "Successfully searched bookreturned!",
                  type: "success",
            });
          })
          .catch((error) => {
            console.log("Searched book failed");
            this.$message({
              message: "Failed to query books！",
              type: "warning",
            });
          }); 
      }
    },
    addByISBN() {
      console.log("isbn:", this.dialogForm.isbn);

      //@GetMapping("/book/getByISBN/{isbn}")
      axios
        .get(`${this.url}/book/getByISBN/${this.dialogForm.isbn}`)
        .then((res) => {
          console.log("getByISBN,sucess");
          console.log(res.data);
          this.dialogForm.author = res.data.author;
          this.dialogForm.bookname = res.data.bookname;
          this.dialogForm.catagory = res.data.catagory;
          this.dialogForm.introduction = res.data.introduction;
          this.dialogForm.press = res.data.press;
          this.dialogForm.publishtime = res.data.publishtime;

          this.$message({
            message: "Successfully searched!",
            type: "success",
          });
        })
        .catch((error) => {
          console.log("getByISBN failed");
          this.$message({
            message: "Failed to getByISBN！",
            type: "warning",
          });
        });
    },
    add() {
      console.log("this.dialogForm.isbn:", this.dialogForm.isbn);
      console.log("this.dialogForm.remain:", this.dialogForm.remain);
      console.log("this.dialogForm.location:", this.dialogForm.location);

      axios //  @GetMapping("/book/addByISBN/{isbn}/{remain}/{location}")
        .get(
          `${this.url}/book/addByISBN/${this.dialogForm.isbn}/${this.dialogForm.remain}/${this.dialogForm.location}`
        )
        .then((res) => {
          console.log("add, success");
          console.log(res.data);

          if (res.data == -1) {
            this.$message({
              message: "接口调用次数达到上限",
              type: "warning",
            });
          } else if (res.data > 0) {
            this.$message({
              message: "Successfully added book!",
              type: "success",
            });
            let bar_codes = [];
            for (let i = 1; i <= this.dialogForm.remain; i++) {
              let a = "http://localhost:8080/barcodes/";
              a += res.data;
              a += "0000";
              a += i;
              a += ".jpg";
              bar_codes[i] = {};
              bar_codes[i].barcode = a;
            }
            console.log("bar_codes:", bar_codes);
            this.bar_codes = bar_codes;
       
          } else {
            this.$message({
              message: "其他情况",
              type: "warning",
            });
          }
        })
        .catch((error) => {
          console.log("Adding book failed", error);
          this.$message({
            message: "Failed to getByISBN!",
            type: "warning",
          });
        });
    },
    cancelAdd() {
           // 重置表单数据

      this.dialogForm.bookname = null;
      this.dialogForm.press = null;
      this.dialogForm.author = null;
      this.dialogForm.publishtime = null;
      this.dialogForm.catagory = null;
      this.dialogForm.remain = null;
      this.dialogForm.introduction = null;
      this.dialogForm.location = null;
      this.dialogForm.isbn = null;
      this.visible = false;
    },
  },
};
</script>

