.book-list {
  font-family: Arial, sans-serif;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  margin-top: 30px;
  margin-bottom: 20px;
  font-size: 28px;
  font-weight: bold;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
}

th {
  background-color: #eee;
  color: #333;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

.borrowing td {
  background-color: #c8e6c9;
}

.returned td {
  background-color: #ffcdd2;
}

.button-container {
  display: flex;
  justify-content: flex-end;
}

.button-group {
  margin-left: auto;
}
</style>